.InternetShops {


  .widgetBlock {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    max-width: 1200px;
    margin: 3rem auto;
  }
  .widgetHeader {
    width: 100%;
    // flex-grow: 1;
    padding-left: 6rem;
    padding-right: 4rem;
    // border: 2px red solid;
    margin: 0;
    align-self: flex-start;
    justify-self: flex-start;

    p {
      max-width: 400px;
      color: #737373;
      text-align: justify;
    }
  }
  .widgetImg {
    width: 100%;

  }
  .tickedBlock {
    margin-top: -2rem;
  }

  .tickedText {
    color: #6A6A6A;
    font-size: 17px;
    font-weight: 500;
    margin: 2rem 1rem;

    &::before {
      top: 2px;
    }
  }
  .paymentMethodsBlock {
    max-width: 1300px;
    margin: 4rem auto;
  }

  .payments {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }

  .paymentsTitle {
    text-align: center;
    margin: 3rem auto;
  }

  .pText {
    margin-bottom: 0px;
    margin-top: 0px;
  }


 @media (max-width: 900px) {
  .tickedText {
    font-size: 1.2rem;
    font-weight: normal;
    color: black;
    margin-left: 3rem;

    &::before {
      top: 6px;
    }
  }
  .widgetHeader {
    text-align: center;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-bottom: 2rem;

    p {
      margin: 0 auto;
    }
  }
 }






}