.Tariffs {

  .topText {
    color: gray;
    font-size: 1.5rem;
  }

  .topImg {
    width: 437px;
  }

  .tariffsBlock {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .tariffsHeader {
    text-align: center;
    padding: 1rem;
  }

  .bottomTextBlock {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .bottomText {
    width: 100%;
    max-width: 1000px;
    text-align: center;
    font-size: 1.5rem;
    color: gray;
    padding: 1rem;
  }
  
  .cardText1 {
    color: gray;
    font-weight: bold;
    font-size: 1.5rem;

  }

  select {
    width: 200px;
    height: 2rem;
    // background-color: #F2F7FC;
    // color: gray;
    font-weight: bold;
    font-size: 1.2rem;
    // border-radius: 10px;
    
  }




  @media (max-width: 900px) {
    .topImg {
      width: 320px;
    }

    .bottomText  {
      font-size: 1rem;
    }
  }


















    
}