.TextWithCircle {

  font-size: 21px;
  font-weight: bold;
  position: relative;
  // border: 2px red solid;

  &::before {
    content: '';
    display: block;
    position: absolute;
    left: -30px;
    top: 6px;
    width: 17px;
    height: 17px;
    border-radius: 50%;
  }

  @media (max-width: 900px)  {
    font-size: 1rem;
    &::before {
      width: 12px;
      height: 12px;
      top: 4px;
    }
  }

}

.violet {

  &::before {
      background-color: #A31FED;
  }

}

.green {
  &::before {
    background-color: #00FF57;
}
}
