.Contacts {
  overflow: hidden;

  .contactsTopBlockWrap {
    position: relative;
    box-sizing: border-box;
    // border: 2px red solid;
    // height: 100vh;
  }

  .contactsTopBlock {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    // margin: 1rem;
    width: 90%;
    max-width: 1400px;
    margin: 1rem;
    color: white;
    // border: 2px red solid;
    // box-sizing: border-box;

   
  }
  .blackBox {
    background-color: #2B2B2B;
    box-shadow: 0px 25px 40px rgba(0, 0, 0, 0.37);
    border-radius: 17px;
    padding: 2rem;
    // box-sizing: border-box;
    max-width: 500px;
  }

  .contactsTopImg {
    width: 100%;
    z-index: -1;
    // box-sizing: border-box;
  }
  .contactsTopImg2 {
    display: none;
    width: 100%;
    z-index: -1;
    // box-sizing: border-box;
  }

  .centered {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 450px;
    // border: 2px red solid;
    max-width: 480px;

    p {
      color: #898989;
    }

  }
  .mailPhone {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    a {
      color: white;
      transition: all 0.2s ease-in-out;
      &:hover {
        color: #A31FED;
      }
    }
  }
  .support {
    padding: 2rem;
  }

  .mailPhoneItem {
    // padding: 0 1rem;
  }

  .map {
    position: absolute;
    top: 100px;
    right: 50px;
    bottom: 100px;
    min-height: 400px;
    min-width: 800px;
    width: 800px;
    // max-width: 50%;

  }
  // .mapContent {
  //   width: 100%;
  //   height: 100%;
  // }

  @media (max-width: 1600px) {
    .map {
      position: static;
      min-width: auto;
      width: 100%;
    }
  } 



  @media (max-width: 1100px) {
   
    .contactsTopBlock {
      bottom: -2rem;
    }
    .centered  {
      min-height: auto;
    }
    // .contactsTopImg2 {
    //   display: block;
    // }
    .contactsTopImg {
      display: none;
    }
    .contactsTopBlockWrap {
      background-image: url('../../img/contacts/contactsTopImg.png');
      background-position: center;
      min-height: 700px;
    }
  
   

  }

  @media (max-width: 600px) {
    .blackBox {
      // max-width: 350px;
      margin-right: 2rem;
      // margin: 1rem auto;
    }
    .support {
      padding-top: 0rem;
    }
   
    .contactsTopImg2  {
      display: none;
    }
    .contactsTopBlock {
      transform: translate(-50%, -70%);
    }
  }



}