.ReceivingPayments {



    .tickedBlock1,
    .tickedBlock2 {
        margin: 1rem;
        // margin-bottom: 1rem;
        width: 45%;
        max-width: 400px;

        @media (max-width: 1500px) {
            width: 100%;
        }

        @media (max-width: 900px) {
            margin-bottom: 0;
            margin-top: 0;
        }
    
        
    }

    .tickedBlocks {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        padding: 2rem;
        max-width: 900px;
        margin: 2rem auto;
        // border: 2px red solid;

        // @media (max-width: 1500px) {
        //  padding: 1rem;

        // }

        @media (max-width: 900px) {
            padding: 1rem;
            padding-left: 2rem;
            margin: 0rem auto;
            h3 {
                font-size: 1rem;
            }
        }
    }

    .tickedText {
        font-size: 21px;
        color: #353535;
    }
    
    .solutionsWrap {
        background-color: hsl(0, 0%, 10%);
        padding-top: 2rem;
        padding-bottom: 2rem;
    }
    .solutionsHeader {
        color: #03EE65;
        margin: 2rem auto;
        text-align: center;
    }

    .solutionsBlock {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        margin-bottom: 2rem;
        color: white;
        max-width: 1400px;
        margin: 2rem auto;

        @media (max-width: 900px) {
            padding: 1rem;
        }
    }

    .howToPayBlock {
        max-width: 1300px;
        margin: 3rem auto;
    }
    .howToPayBlockContent {
        display: flex;
        flex-wrap: wrap;
        margin-top: 3rem;
        justify-content: space-evenly;
        // align-content: center;
    }
    .contentItem {
        max-width: 500px;
    }
    .widgetImg {
        width: 100%;
        max-width: 350px;
        margin-left: 0.5rem;
        transition: all 0.2s ease-in-out;
        
    }

    .fadeIn {
        animation: fade  1s  ease-in-out;
    }

    @keyframes fade {
        from {
            opacity: 0;
        }
        to {
            opacity: 1;
        }
        }
   
    
}