.HowToPayStep {
  display: flex;
  margin-bottom: 2rem;
  margin-left: 1rem;
  margin-right: 1rem;
  cursor: pointer;
  border: 1px solid transparent;
  border-radius: 28px;
  transition: all 0.2s ease-in-out;
  opacity: 0.7;

  &:hover {
    border: 1px solid #A31FED;
    box-sizing: border-box;
    opacity: 1;
    box-shadow: 0 0 20px hsla(278, 85%, 53%, 0.2); /* Параметры тени */
  }

  .number {
    color: #A31FED;
    font-weight: bold;
    font-size: 48px;
    line-height: 33px;
    align-self: center;
    margin-right: 1.5rem;
    padding-left: 1rem;
  }
  
  .text {
    line-height: 33px;
    padding-right: 1rem;

  }


  
}


.active {
  border: 1px solid #A31FED;
  box-sizing: border-box;
  opacity: 1;
  box-shadow: 0 0 20px hsla(278, 85%, 53%, 0.2); /* Параметры тени */
}