.OnlineStudy {

  .advantagesBlock {
    max-width: 1300px;
    margin: 3rem auto;
    p {
      color: gray;
      font-weight: 500;
      // margin: 0;
      @media (max-width: 1280px) {
        color: black;
      }
    }

    h2 {
      font-size: 23px;
    }
  }

  .advantagesBlockContent, 
  .possibilitiesBlockContent {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }

  .possibilitiesBlockWrap {
    position: relative;
  }

  .possibilitiesBlock {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    max-width: 1300px;
    margin: 3rem auto;
    color: #FFFFFF;
    // padding: 1rem;

    p {
      // color: gray;
      font-weight: bold;
      font-size: 19px;
      margin-top: 4rem;
      // margin: 0;
      @media (max-width: 1280px) {
        margin-top:  0.83em;
      }
    }

    h2 {
      // color: gray;
      font-weight: bold;
      font-size: 19px;
      // margin-top: 3rem;
    }
  }

  .possibilitiesBlockContent {
    margin-top: 4rem;
  }

  .possibilitiesBGImage {
    width: 100%;
  }

  @media (max-width: 1300px) {
    .possibilitiesBlockWrap  {
      background-color: hsl(0, 0%, 10%);
    }
    .possibilitiesBlock {
      position: static;
      transform: unset;
      padding: 3rem 0;


    }
    .possibilitiesBGImage {
      display: none;
    }
  }



}