.CardWithButton {

  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  max-width: 1000px;
  padding: 1rem;
  margin: 1rem;
  border: 7px solid gray;
  border-radius: 28px;
  box-sizing: border-box;
  background-color: white;
  


  


  .cardColumn {
    display: flex;
    flex-direction: column;
    // border: green 1px solid;
   
    width: 48%;
    margin-bottom: 1rem;
  }

  .image {
    width: 100%;
    // border: green 1px solid;
  }

  


  @media (max-width: 900px) {

    width: 100%;
    max-width: 350px;

    .cardColumn {
      width: 100%;
      justify-content: center;
      align-items: center;

    }
    // .image {
    //   width: 260px;
    // }
    .header {
      text-align: center;
    }
  }




}