.FZ54 {


  .cashierBlock1 {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 2rem auto;
    // max-width: 1200px;
  }

  .cashierBlock2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 2rem auto;
    margin-left: 10rem;
    // max-width: 1200px;
    // border: 2px red solid;
    @media (max-width: 1350px) {
      margin-left: 0rem;
    }
  }
 
  .contentStyles {
    max-width: 350px !important;
    min-height: 230px;
    text-align: left !important;
    h3 {
      font-weight: normal;
      font-size: 16px;
      margin-left: 1rem;

      &::before {
        top: 1px;
      }
    }
  }

  .cashierImg {
    width: 440px;
    // width: 100%;
    @media (max-width: 900px) {
      display: block;
      width: 100%;
      max-width: 440px;
      margin: 0 auto;
    }
  }

  .cashierImgHidden {
    @media (max-width: 900px) {
      display: none;
    }
  }

  .tickedText {
    font-size: 1.2rem;
    font-weight: 600;
    margin-left: 1rem;
  }

  .cashierText {
    font-size: 1.3rem;
    line-height: 2rem;
  }

  .orangeData {
    color: #A31FED;
    font-size: 1.9rem;
  }






















    
}