.Credits {

  .creditsInfo {
    background-color: #464646;
    padding: 1rem;
    margin-top: -3px;
    color: white;

  }
  .creditsInfoContent {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    max-width: 1400px;
    margin: 0 auto;
    text-align: center;
  }
  .creditsInfoItem {
    max-width: 400px;
  }

  .headerStyles {
    text-align: center;
  }

    .advantages, .loanCalculationBlock, .loanConditions {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    .advantages {
      justify-content: space-evenly;
      max-width: 1200px;
      margin: 0 auto;
    }
    .tickedTextForWhome {
      font-size: 1.2rem;
      margin-left: 1rem;
      margin-bottom: 2rem;

      span {
        display: inline-block;
        color: #807CFA;
        margin-bottom: 1rem;
      }
    }

    .advantagesBlock {
      max-width: 400px;
      margin: 0rem auto;
      margin-top: 2rem;
      padding: 1rem;
    }

   .loanConditions {
      justify-content: space-evenly;
      margin: 3rem auto;

   }

    .loanHeader {
      text-align: center;
    }
    .loanContentStyles {
      text-align: left;
      max-width: 350px;
    }
    .blackCardStyle {
      max-width: 450px;
      min-height: 450px;
    }

    .loanText {
      font-size: 1.2rem;
      font-weight: normal;
      margin-left: 1rem;
    }

    .loanCalculationBlock {
      padding: 2rem;
      color: white;
    }

    .loanCalculationCard {
      width: 80%;
      border-radius: 49px;
      margin: auto;
      padding: 1rem;
      background-color: #B097BF;
      
    }

    .rangeBlock {
      text-align: center;
      margin-bottom: 2rem;
    }

    .rangeWrapper {
      
    }

    .resultBlock {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      margin: 6rem;
    }

    .resultItem {
      width: 40%;
    }

    


    @media (max-width: 900px) {
      .loanCalculationBlock {
        padding: 0;
        padding-bottom: 1rem;
      }
      .resultBlock {
        margin: 1rem;
      }
      .resultItem {
        width: 100%;

      }
    }




}