.Partners {


  .talkingPeopleBlock {
    justify-content: center;
    align-items: center;
  }

  .inputRangeBlock {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    color: white;
    background-color: #2B2B2B;
    padding: 4rem 2rem;
    // margin: 1rem;
    // border: 7px solid #D3D2D4;
    // border-radius: 28px;
    box-shadow: 25px 25px 30px rgba(0, 0, 0, 0.25);
    // text-align: center;
    max-width: 1000px;
    margin: 1rem auto;
    min-height: 400px;
  }

  .rangeHeader {
    width: 90%;
    margin: 0 auto;
     h1, p {
       max-width: 440px;
     }
  }

  .turnOver {
    text-transform: uppercase;
    font-weight: bold;
    text-align: center;
  }

  .resultBlock {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    margin: 0 auto;

    p {
      text-align: left;
    }
  }

  .btnWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .result {
    // display: inline-block;
    font-size: 2rem;
    font-weight: bold;
    // width: 142px;
    color: #03EE65;
    text-align: left;
  }

  .talkingPeopleImg {
    text-align: center;
  }
  .advantagesList {
    max-width: 1100px;
    margin: 0 auto;

  }


  @media (max-width: 900px) {
    .talkText {
      font-size: 1.2rem;
      text-align: center;
    }
    .btnWrapper {
      width: 100%;
    }

    .inputRangeBlock {
      padding: 1rem;
      padding-bottom: 2rem;
    }
    .resultBlock {
      p {
        text-align: center;
        margin: 2rem auto;
      }
    }
    .rangeHeader {
      text-align: center;
      h1, p {
        margin: 2rem auto;
      }
      h1 {
        font-size: 23px
      }
    }
  }





























    
}