.BlackCard {
 
  max-width: 370px;
  width: 100%;
  min-height: 380px;
  background-color:  #2B2B2B;
  border: 7px solid #2B2B2B;
  border-radius: 30px;
  margin: 1rem;
  padding: 1rem;
  transition: all 0.2s ease-in-out;
  cursor: pointer;

  &:hover {
    border: 7px solid #807CFA;
  }

  .cardColumn {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
  }

  .header {
    font-size: 21px;
    text-align: center;
    color: #03EE65;
  }

  .h3 {
    color: white;
    text-align: center;
  }
  .content {
    color: white;
    text-align: center;
    max-width: 250px;
    margin: 0 auto;
  }

  .button {
    text-align: center;
  }




  @media (max-width: 900px) {
    min-height: 250px;

    .content {
      text-align: center;
    }
    
  }






}